/* App.css */
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.container {
  display: flex;
  height: 700px;
  border: 1px solid #000;
  justify-content: center;
  align-items: flex-start;
  padding: 20px;
}

.photoview {
  flex: 2;
  border: 1px solid red;
  width: 100%;
  position: relative;
  height: 80%;
}
.right-side {
  flex:1
}

.image-list-container {
  
  display: flex;
  flex-direction: column;
  height: 27vw;
  /* border: 1px solid yellow; */
}
.title-wholeDelete{
  right: 20px;
  margin-bottom: 20px;
}
.image-list {
  margin-left: 20px;
  border: 1px solid blue;
  
  flex:1;
  height: 100%;
  
  

}

.image-gallery {
  
  width: 100%;
  height: 100%;
  max-width: 600px;
  margin: auto;
}

.image-gallery img {
  width: 100%;
  height: 100%;
}

.image-gallery .prev, .image-gallery .next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  z-index: 1;
}

.image-gallery .prev {
  left: 10px;
}

.image-gallery .next {
  right: 10px;
}

.current-image {
  font-weight: bold;
  color: rgb(39, 228, 73);
  
}

.deleteButton{
  right: 0;
}

.pagination-container {
  display: flex;
  justify-content: center;
  margin-top: auto;
  
}

.pagination {
  display: flex;
  justify-content: center;
}

.pagination button, .pagination span {
  margin: 0 5px;
  padding: 5px 10px;
}

.pagination .active-page {
  font-weight: bold;
  color: white;
  background-color: blue;
}



.image-list {
  flex: 1;
  overflow-y: auto;
}

.image-list ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.image-list ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title-wholeDelete {
  margin-left: 13vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title-wholeDelete h2 {
  margin: 0;
  text-align: center;
}

.title-wholeDelete button {
  color: red;
}

/* 반응형 디자인을 위한 미디어 쿼리 */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
  }

  .photoview, .right-side {
    max-width: 100%;
    margin: 10px 0;
  }

  .image-gallery img {
    max-height: 300px;
  }
}

@media (max-width: 480px) {
  .prev, .next {
    padding: 5px;
  }

  .image-gallery img {
    max-height: 200px;
  }
}